import './App.css';

import {
  BrowserRouter as Router,
  Route,
  Switch,
} from 'react-router-dom'

import HomePage from './pages/HomePage'
import AboutPage from './pages/AboutPage'
import ArticlesListPage from './pages/ArticlesListPage'
import ArticlePage from './pages/ArticlePage'
import NavBar from './NavBar'
import NotFoundPage from './pages/NotFoundPage';

function App() {
  return (
    <Router>
    <div className="App">
      <NavBar />
      <div id="page-body">
      <Switch>
      <Route path="/" component={HomePage} exact></Route>
      <Route path="/about" component={AboutPage}></Route>
      <Route path="/articles-list" component={ArticlesListPage}></Route>
      <Route path="/article/:name" component={ArticlePage}></Route>
      {/* :name allows "name" passed as a prop to the component  */}
      <Route component={NotFoundPage}></Route> 
      {/* route without a path will always match by default */}
      </Switch>
      </div>
    </div>
    </Router>
  );
}

export default App;
